@use '../vars';

div#about {
	div.container {
		div.bottomLine {
			margin: 0 auto 3rem auto;
		}

		div.info {
			display: grid;
			grid-template-areas:
				"image bio bio"
				"award1 award2 award3";
			grid-gap: 1.8rem;

			.bioImage {
				display: grid;
				grid-template-columns: repeat(2, 1fr);
				grid-area: image;
			}

			.bio {
				grid-area: bio;
				border-left: 2px solid vars.$mainColor;
				padding: 1rem;

				h4 {
					font-size: 2.3rem;
				}

				p {
					font-size: 1.8rem;
					text-align: left;
					margin: 1.5rem 0;
				}
			}

			.award1 {
				grid-area: award1;
			}

			.award2 {
				grid-area: award2;
			}

			.award3 {
				grid-area: award3;
			}

			.award1,
			.award2,
			.award3 {
				svg {
					font-size: 4rem;
					color: vars.$mainColor;
					margin: 1.5rem;
				}

				h3 {
					font-size: 2.1rem;
				}

				p {
					font-size: 1.5rem;
				}
			}
		}
	}
}

@media (max-width: 1050px) {
	div#about {
		div.container {
			div.info {
				display: grid;
				grid-template-areas:
					"bio"
					"image"
					"award1"
					"award2"
					"award3";

				.bioImage {
					grid-template-columns: repeat(4, 1fr);
					grid-area: image;
				}

				.bio {
					border-left: none;
					border-bottom: 2px solid vars.$mainColor;
				}
			}
		}
	}
}

@media (max-width: 500px) {
	div#about {
		div.container {
			div.info {
				grid-template-areas:
					"bio"
					"image"
					"award1"
					"award2"
					"award3";

				.bioImage {
					display: grid;
					grid-template-columns: repeat(2, 1fr);
				}

				.bio {
					border-left: none;
					border-bottom: 2px solid vars.$mainColor;
				}
			}
		}
	}
}
